/**
 * 
 * @param {object} obj 
 *  {
 *      title: '提示的内容',
 *      icon: 图标，有效值 'success', 'none',
 *      duration: 提示的延迟时间，单位毫秒，默认：1500,
 *      mask: 是否显示透明蒙层，防止触摸穿透，默认：true
 *  }
 *  返回Promise
 */ 
export function showToast (obj) {
    let {
        title = '', 
        icon = 'success', 
        image, 
        duration = 1500, 
        mask = true
    } = obj || {}
    return new Promise((resolve, reject) => {
        wx.showToast({
            title,
            icon,
            image,
            duration,
            mask,
            success: resolve,
            fail: reject
        })
    })
}

/**
 * 
 * @param {object} obj 
 *  {
 *      title: 提示的标题,
 *      content: 提示的内容,
 *      showCancel: 是否显示取消按钮，默认为 true,
 *      cancelText: 取消按钮的文字，默认为"取消"，最多 4 个字符,
 *      cancelColor: 取消按钮的文字颜色，默认为"#000000",
 *      confirmText: 确定按钮的文字，默认为"确定"，最多 4 个字符,
 *      confirmColor: 确定按钮的文字颜色，默认为"#00aaff",
 *  }
 *  返回Promise
 */ 
export function showModal (obj) {
    let {
        title = '',
        content = '', 
        showCancel = true, 
        cancelText = '取消', 
        cancelColor = '#000000', 
        confirmText = '确定', 
        confirmColor = '#00aaff'
    } = obj || {}
    return new Promise((resolve, reject) => {
        wx.showModal({
            title,
            content,
            showCancel,
            cancelText,
            cancelColor,
            confirmText,
            confirmColor,
            success: resolve,
            fail: reject
        })
    })
}

/**
 * 
 * @param {object} obj 
 *  {
 *      timeStamp: 时间戳，从 1970 年 1 月 1 日 00:00:00 至今的秒数，即当前的时间
 *      nonceStr: 随机字符串，长度为32个字符以下
 *      package: 统一下单接口返回的 prepay_id 参数值，提交格式如：prepay_id=***
 *      signType: 签名算法 默认 MD5
 *      paySign: 签名
 *  }
 *  返回Promise
 */ 
export function requestPayment (obj) {
    let {
        timeStamp,
        nonceStr,
        package: Package,
        signType = 'MD5',
        paySign
    } = obj || {}
    return new Promise((resolve, reject) => {
        wx.requestPayment({
            timeStamp,
            nonceStr,
            package: Package,
            signType,
            paySign,
            success: resolve,
            fail: reject
        })
    })
}


/**
 * 判断两个数组是否相等
 * @param {array} arr1 数组一
 * @param {array} arr2 数组二
 */
export function arrayEqual(arr1, arr2) {
    if (arr1 === arr2) return true
    if (arr1.length != arr2.length) return false
    for (let i = 0; i < arr1.length; ++i) {
        if (arr1[i] !== arr2[i]) return false
    }
    return true
}

/**
 * 判断`obj`是否为空
 * @param {object} obj
 */
export function isEmptyObject(obj) {
    if (!obj || typeof obj !== 'object' || Array.isArray(obj)) return false
    return !Object.keys(obj).length
}

/**
 * 随机生成颜色
 */
export function randomColor() {
    return '#' + ('00000' + (Math.random() * 0x1000000 << 0).toString(16)).slice(-6)
}

/**
 * 生成指定范围随机数
 * @param {number} min
 * @param {number} max
 */
export function randomNum(min, max) {
    return Math.floor(min + Math.random() * (max - min))
}

/**
 * 判断是否为身份证
 * @param {string} str 身份证号
 */
export function isIdCard(str) {
    return /^(^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$)|(^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[Xx])$)$/.test(str)
}

/**
 * 银行卡每四位+空格
 * @param {string} s 银行卡号
 */
export function bankSpace (s) {
    if (/^[\d]{0,19}$/.test(s.replace(/\s/g, ''))) {
        //每四位数字加一个空格
        s = s.replace(/\D/g, '').replace(/(....)(?=.)/g, '$1 ')
    }
    return s
}

/**
 * 检测是否是新版本
 */
export function isNew () {
    // 检测版本
    const updateManager = wx.getUpdateManager()
    updateManager.onUpdateReady( async () =>{
        let {
            confirm
        } = await showModal({
            title: '更新提示',
            content: '新版本已经准备好，是否重启应用？',
        })
        if (confirm) {
            updateManager.applyUpdate()
        }
    })  
}

